--root {
  flex-grow: 1;
  flex-shrink: 0;
  flex-basis: auto;
}

body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  width: 100vw;
  height: 100vh;
  background-color: #414141;
}

.flex {
  display: flex;
}
.flex_direction--row {
  flex-direction: row;
}
.flex_direction--row-reverse {
  flex-direction: row-reverse;
}
.flex_direction--column {
  flex-direction: column;
}
.flex_direction--column-reverse {
  flex-direction: column-reverse;
}
.flex_align-items--center {
  align-items: center;
}
.flex_align-items--flex-end {
  align-items: flex-end;
}
.flex_align-items--flex-start {
  align-items: flex-start;
}
.flex_align-items--stretch {
  align-items: stretch;
}
.flex_align-items--baseline {
  align-items: baseline;
}
.flex_align-self--center {
  align-self: center;
}
.flex_align-self--flex-end {
  align-self: flex-end;
}
.flex_align-self--flex-start {
  align-self: flex-start;
}
.flex_align-self--stretch {
  align-self: stretch;
}
.flex_align-self--baseline {
  align-self: baseline;
}
.flex_justify-content--center {
  justify-content: center;
}
.flex_justify-content--flex-start {
  justify-content: flex-start;
}
.flex_justify-content--flex-end {
  justify-content: flex-end;
}
.flex_justify-content--space-between {
  justify-content: space-between;
}
.flex_justify-content--space-around {
  justify-content: space-around;
}
.flex_justify-content--space-evenly {
  justify-content: space-evenly;
}
.flex_wrap--wrap {
  flex-wrap: wrap;
}
.flex_wrap--no-wrap {
  flex-wrap: nowrap;
}
.flex_wrap--wrap-reverse {
  flex-wrap: wrap-reverse;
}
.flex_align-content--flex-start {
  align-content: flex-start;
}
.flex_align-content--flex-end {
  align-content: flex-end;
}
.flex_align-content--center {
  align-content: center;
}
.flex_align-content--space-between {
  align-content: space-between;
}
.flex_align-content--space-evenly {
  align-content: space-evenly;
}
.flex_align-content--space-around {
  align-content: space-around;
}
.flex_align-content--stretch {
  align-content: stretch;
}
.flex-item--flex-grow {
  flex-grow: var(--flex-grow);
}
.flex-item--flex-shrink {
  flex-shrink: var(--flex-shrink);
}
.flex-item--flex-basis {
  flex-basis: var(--flex-grow);
}
.flex-item--flex {
  flex-basis: var(--flex-grow) var(--flex-shrink) var(--flex-grow);
}
